"use client";

import { useState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";

// Mock orders data
const mockOrders = [
  {
    id: "1",
    items: [
      { name: "Surgical Masks", quantity: 5000 },
      { name: "Disposable Gloves", quantity: 2000 },
    ],
    status: "pending",
    priority: "high",
    requestedBy: "Dr. John Smith",
    department: "Surgery",
    requestDate: "2024-01-15",
    totalAmount: "$2,500",
  },
  {
    id: "2",
    items: [
      { name: "Antibiotics", quantity: 100 },
      { name: "Pain Medication", quantity: 200 },
    ],
    status: "approved",
    priority: "medium",
    requestedBy: "Dr. Sarah Johnson",
    department: "Pharmacy",
    requestDate: "2024-01-14",
    totalAmount: "$5,000",
  },
  {
    id: "3",
    items: [
      { name: "Bandages", quantity: 1000 },
      { name: "Antiseptic Solution", quantity: 50 },
    ],
    status: "delivered",
    priority: "normal",
    requestedBy: "Dr. Mike Wilson",
    department: "Emergency",
    requestDate: "2024-01-13",
    totalAmount: "$1,200",
  },
];

const statusColors = {
  pending: "bg-yellow-100 text-yellow-800",
  approved: "bg-blue-100 text-blue-800",
  delivered: "bg-green-100 text-green-800",
};

const priorityColors = {
  high: "bg-red-100 text-red-800",
  medium: "bg-orange-100 text-orange-800",
  normal: "bg-gray-100 text-gray-800",
};

export default function OrdersPage() {
  const [showNewOrder, setShowNewOrder] = useState(false);
  const [searchTerm, setSearchTerm] = useState("");
  const [selectedStatus, setSelectedStatus] = useState("all");

  const filteredOrders = mockOrders.filter(
    (order) =>
      (selectedStatus === "all" || order.status === selectedStatus) &&
      (order.department.toLowerCase().includes(searchTerm.toLowerCase()) ||
        order.requestedBy.toLowerCase().includes(searchTerm.toLowerCase()))
  );

  return (
    <div className="space-y-6">
      <div>
        <h2 className="text-3xl font-bold tracking-tight">Orders</h2>
        <p className="text-muted-foreground">
          Manage and track supply orders
        </p>
      </div>

      <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Total Orders</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{mockOrders.length}</div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Pending</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-yellow-600">
              {mockOrders.filter((o) => o.status === "pending").length}
            </div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Approved</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-blue-600">
              {mockOrders.filter((o) => o.status === "approved").length}
            </div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Delivered</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-green-600">
              {mockOrders.filter((o) => o.status === "delivered").length}
            </div>
          </CardContent>
        </Card>
      </div>

      <Card>
        <CardHeader>
          <CardTitle>Manage Orders</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="flex flex-col md:flex-row gap-4 mb-6">
            <Input
              placeholder="Search by department or requester..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="max-w-sm"
            />
            <select
              value={selectedStatus}
              onChange={(e) => setSelectedStatus(e.target.value)}
              className="border rounded-md px-3 py-2"
            >
              <option value="all">All Status</option>
              <option value="pending">Pending</option>
              <option value="approved">Approved</option>
              <option value="delivered">Delivered</option>
            </select>
            <Button onClick={() => setShowNewOrder(!showNewOrder)}>
              + New Order
            </Button>
          </div>

          {showNewOrder && (
            <Card className="mb-6">
              <CardContent className="pt-6">
                <form className="space-y-4">
                  <div className="grid gap-4 md:grid-cols-2">
                    <div className="space-y-2">
                      <Label htmlFor="department">Department</Label>
                      <Input id="department" placeholder="Select department" />
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="priority">Priority</Label>
                      <select
                        id="priority"
                        className="w-full border rounded-md px-3 py-2"
                      >
                        <option value="normal">Normal</option>
                        <option value="medium">Medium</option>
                        <option value="high">High</option>
                      </select>
                    </div>
                  </div>

                  <div className="space-y-2">
                    <Label>Items</Label>
                    <Card>
                      <CardContent className="pt-4">
                        <div className="space-y-2">
                          <div className="flex gap-2">
                            <Input placeholder="Item name" className="flex-1" />
                            <Input
                              type="number"
                              placeholder="Quantity"
                              className="w-32"
                            />
                            <Button variant="outline">+</Button>
                          </div>
                        </div>
                      </CardContent>
                    </Card>
                  </div>

                  <div className="flex justify-end gap-2">
                    <Button
                      variant="outline"
                      onClick={() => setShowNewOrder(false)}
                    >
                      Cancel
                    </Button>
                    <Button>Create Order</Button>
                  </div>
                </form>
              </CardContent>
            </Card>
          )}

          <div className="rounded-md border">
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Order ID</TableHead>
                  <TableHead>Department</TableHead>
                  <TableHead>Items</TableHead>
                  <TableHead>Status</TableHead>
                  <TableHead>Priority</TableHead>
                  <TableHead>Requested By</TableHead>
                  <TableHead>Date</TableHead>
                  <TableHead>Amount</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {filteredOrders.map((order) => (
                  <TableRow key={order.id}>
                    <TableCell className="font-medium">#{order.id}</TableCell>
                    <TableCell>{order.department}</TableCell>
                    <TableCell>
                      <div className="max-w-[200px] truncate">
                        {order.items.map((item) => item.name).join(", ")}
                      </div>
                    </TableCell>
                    <TableCell>
                      <span
                        className={`px-2 py-1 rounded-full text-xs ${
                          statusColors[order.status as keyof typeof statusColors]
                        }`}
                      >
                        {order.status}
                      </span>
                    </TableCell>
                    <TableCell>
                      <span
                        className={`px-2 py-1 rounded-full text-xs ${
                          priorityColors[
                            order.priority as keyof typeof priorityColors
                          ]
                        }`}
                      >
                        {order.priority}
                      </span>
                    </TableCell>
                    <TableCell>{order.requestedBy}</TableCell>
                    <TableCell>{order.requestDate}</TableCell>
                    <TableCell>{order.totalAmount}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
